import React from 'react';
import './navbar.css';
import { menuItems } from './store';
import NavbarItem from "../navbar-item";

class Navbar extends React.Component {
    render() {
        return (<nav className="navbar navbar-default navbar-fixed-top">
                <div className="container">
                    <div className="navbar-header">
                        <button type="button" className="navbar-toggle collapsed" data-toggle="collapse"
                                data-target="#navbar__collapse" aria-expanded="false">
                            <span className="sr-only">Меню</span>
                            <span className="icon-bar"></span>
                            <span className="icon-bar"></span>
                            <span className="icon-bar"></span>
                        </button>

                        <a className="navbar-brand" href="/">
                            SolarLab<span>.</span>
                        </a>

                    </div>
                    <div className="collapse navbar-collapse" id="navbar__collapse">
                        <ul className="nav navbar-nav navbar-left">
                            { menuItems.map( item => <li className={ item.submenuItems.length > 0 ? 'dropdown' : null} key={ item.id }><NavbarItem item={ item }></NavbarItem></li>) }
                        </ul>
                        <ul className="nav navbar-nav navbar-right" style={{display: 'none'}}>
                            <li><a href="#signinModal" data-toggle="modal"><i className="ion-log-in"></i> Sign In</a>
                            </li>
                            <li><a href="#signupModal" data-toggle="modal"><i className="ion-android-person"></i> Sign
                                Up</a></li>
                            <li className="hidden-xs">

                                <a href="#" className="navbar-search__toggle">
                                    <i className="ion-search"></i>
                                </a>

                                <div className="navbar-search">
                                    <form>

                                        <div className="navbar-search__box">
                                            <div className="input-group">
                                                <input type="text" className="form-control" placeholder="Search"/>
                                                <span className="input-group-btn">
                        <button type="submit" className="btn btn-accent"><i className="ion-search"></i></button>
                      </span>
                                            </div>
                                        </div>

                                    </form>
                                </div>

                            </li>
                        </ul>
                    </div>

                </div>
            </nav>
        )
    };
}

export default Navbar;